import '../styles/tokens-grid.css'
interface TokensGridPorps {
    tokens: Record<string,string>,
    hasRemValue?: boolean
}

export function TokensGrid({ tokens, hasRemValue=false } : TokensGridPorps) {
    return (
        <table className="tokens-grid">
            <thead>
                <tr>
                    <th>Name</th>
                    <th>Value</th>
                    {hasRemValue && <th>Pixel</th>}
                </tr>
            </thead>
            <tbody>
                {Object.entries(tokens).map(([key,value]) =>{
                    return(
                      <tr key={key}>
                        <td>{key}</td>
                        <td>{value}</td>
                        {hasRemValue && (
                            <td>{Number(value.replace('rem','')) *16}px</td>
                        ) }
                      </tr>  
                    )
                })}
            </tbody>
        </table>
    )
}